body{ 
    display: flex;
    justify-content: center; 
    align-items: center; 
    height: 100vh; 
    margin:0; 
    /* background-image: url('background.svg'); */
}  

.navbar{ 
    width: 100%; 
    min-height: 80px; 
    background-color: white; 
    display: flex; 
    justify-content: center;
    align-items: center;
    box-sizing: border-box; 
    position:relative; 
} 
    .nav_button_container {
        width: 40%; 
        display: flex; 
        justify-content: center; 
        align-items: center;
        border: solid 2px #0072ff; 
        border-radius: 40px; 
        margin: 1em; 
        min-width: 330px; 
        min-height: 40px; 
        max-width: 600px;
    } 
    .navbar img { 
        display: inline;
        position: absolute; 
        left: 2%;
        height:50px;
    }
    .navbar button { 
        color: #0072ff; 
        font-size: 1.1em; 
        height: auto; 
        background-color: transparent; 
        border-color: transparent;
        margin: auto; 
        padding: .8%; 
        position: relative; 
        text-wrap: nowrap; 
    } 
    .navbar button::after {
        content: ''; 
        position: absolute; 
        bottom: 0; 
        left: 50%; 
        transform: translateX(-50%); 
        width: 0; 
        height: 4px; 
        background-color: #0072ff;
        transition: width 0.2s linear; 
    } 
    .navbar button:hover::after { 
        width: 50%; 
    } 
    .web_select { 
        display: block;
        position: absolute; 
        right:2%; 
        color:white; 
        padding: 0.5em 0.8em 0.5em 0.8em; 
        border-radius: 10px; 
        background-color: #0072ff;
        max-width: 200px; 
        font-size: 0.85em; 
        font-weight: lighter;
        font-family: 'Arial Narrow', Arial, sans-serif;
    } 
    .web_select option{ 
        background-color: white; 
        color:#0072ff; 
    } 
    @media (max-width: 700px) { 
        .navbar img { 
            height:40px; 
        } 
        .navbar select { 
            width: 13% ; 
        } 
    }