.main{ 
    width: 100vw; 
    height: 100vh;
    display: flex; 
    flex-direction: column;
    justify-content: flex-start; 
}
    .hero {  
        background: navy;    
        display: flex;
        justify-content: center; 
        align-items: center;
        height: 10%; 
        font-size: 3em; 
        min-height: 70px;
    } 
    p{ 
        font-family: "Archivo Black", sans-serif;
        font-weight: bolder;
        /* background-image: linear-gradient(45deg, rgb(255, 136, 0),#bd2a2a);  */
        background-clip: text; 
        color: #c3ffb7; 
    } 
    .content {
        height: 80%; 
        width: 100%;
        text-align: center; 
    } 
    
.upload{ 
    display: flex; 
    flex-direction: column; 
    align-items: center;
    width: 10%;
    position:absolute; 
    right: 8%; 
} 
    .upload label{ 
        font-family: Arial, Helvetica, sans-serif; 
        font-weight: normal;
        display: flex; 
        justify-content: center; 
        align-items: center; 
        font-weight: bold;
    }
    .upload input[type="file"] { 
        display: none;
    } 
    .upload label{ 
        padding: 0.1em 0.2em;
        background-color: white; 
        border:solid 2px darkgray; 
        font-size: 0.4em; 
        color: #0072ff; 
        text-align: center; 
        border-radius: 10px;
        margin: .2em; 
        text-wrap: nowrap;  
        width: fit-content;
        max-width: 200px; 
        overflow:hidden; 
    }
    .upload label:hover{ 
        background-color: #ffb7b7; 
    } 
        .upload img{ 
            width: 40px; 
            margin-right: 0.2em;
        }
.thumbnail { 
    border-radius: 10px;
    max-width: 13vw; 
    max-height: 13vw; 
    margin: 1em; 
    min-width: 110px; 
    min-height: 110px; 
} 

.thumbnail:hover {
    opacity: 0.6; 
} 

h2 { 
    text-align: center; 
    text-decoration: underline;
} 
.gallary_container { 
    margin-right: 5%; 
    margin-left: 5%; 
} 


@media (max-width: 700px) { 
    .upload label { 
        font-size: 0.0em; 
    } 
    .upload img{ 
        width: 50px; 
    } 
} 