body{ 
    display: flex;
    justify-content: center; 
    align-items: center; 
    height: 100vh; 
    margin:0; 
    width:auto;
}  

.welc{ 
    display: flex; 
    justify-content: center; 
    height: 80%; 
    width: 100%; 
    margin:auto; 
}
    .side_by_side { 
        display: grid; 
        grid-template-columns: 1fr 1fr;
        gap:0; 
        height: 100%;  
        width: 1200px; 
        margin:auto; 
    } 
        .moto{ 
            color:#0072ff; 
            font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; 
            display: flex;
            flex-direction: column; 
            align-items: center; 
            height: 100%; 
            width: 100%; 
        } 
            .moto h1{ 
                font-size: 4em; 
                margin-bottom: 0; 
            } 
            .moto h3{ 
                font-size: 1.3em;
            } 
            .moto_buttons{ 
                display: grid; 
                grid-template-columns: 1fr 1fr; 
                height: 10%; 
                width: 100%; 
            }
                .moto_buttons button { 
                    margin: auto; 
                    background-color: transparent; 
                    border-color: transparent;
                    background-color: white; 
                    border: solid 2px #0072ff;
                    height: 50px; 
                    width: 150px; 
                    position: relative; 
                    color: #0072ff; 
                    font-size: 1em; 
                    transition: all 0.2s ease; 
                    font-family: Tahoma, Geneva, Verdana, sans-serif; 
                } 
                .moto_buttons button::before { 
                    content: ''; 
                    position: absolute; 
                    bottom: 0; 
                    left: 0; 
                    transform: translateX(4%) translateY(13%); 
                    width: 100%; 
                    height:100%; 
                    background-color: #0072ff; 
                    transition: all 0.19s ease; 
                    z-index: -1;
                } 

                .moto_buttons button:hover::before { 
                    content: ''; 
                    position: absolute; 
                    bottom: 0; 
                    left: 0; 
                    transform: translateX(0%) translateY(0%); 
                    width: 100%; 
                    height: 100%; 
                } 
        .welc_images{ 
            display: block;
            width: 100%; 
            height: 100%;
            margin-top:auto; 
            margin-bottom:auto; 
        } 
            .welc_images img{ 
                width: 100%; 
                display: block;
                height: auto; 
            } 
        @media (max-width: 1300px) { 
            .side_by_side { 
                grid-template-columns: 1fr; 
                width: 80%; 
                height: 100%; 
            } 
            .welc_images { 
                width: 80%; 
                display: block; 
                margin: auto; 
            } 
            .moto { 
                margin-bottom: 2em; 
            }
        } 